@import url(https://assets.hackclub.com/fonts/Phantom_Sans_0.7/Bold.woff);
* {
  margin: 0;
}
a {
  text-decoration: none;
}
.landing {
  height: 100vh;
  font-family: "Phantom Sans";
  color: #ffffff;
  overflow: hidden;
  box-sizing: content-box;
  background: linear-gradient(180deg, transparent, white) ,url(/static/media/BG.f76179d8.png);
  background-color: white;
}

.landing__content {
  font-size: 4rem;
  font-weight: 900;
  margin-top: 25vh;
  display: grid;
  -webkit-align-items:center;
          align-items:center;
  color: #EC3750;
}

.s1{
  margin-left: 30vw;
}
.s2{
  margin-left: 35vw;
}
.s3{
  margin-left: 40vw;
}

.paral {
  display: inline-block;
  -webkit-transform: skew(20deg);
          transform: skew(20deg);
  background: linear-gradient(90deg, #EC37A5 -22.7%, #EC3761 79.23%);
  width: 280px;
  margin-left: 50vw;
}

.paral .paraltext {
  display: block;
  -webkit-transform: skew(-20deg);
          transform: skew(-20deg);
  color: white;
  margin-left: 8%;
}

.paral1 {
  display: inline-block;
  -webkit-transform: skew(20deg);
          transform: skew(20deg);
  background: #FF8C37;
  width: 150px;
}
.paral1 .paraltext1 {
  display: block;
  -webkit-transform: skew(-20deg);
          transform: skew(-20deg);
  color: white;
  margin-left: 5%;
}

@media only screen and (max-width: 792px) {
  .landing__content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .s1, .s2, .s3, .paral {
    margin-left: unset;
  }

  h2 {
    font-size: 1.2em;
  }

  .paral1 {
    width: 108%;
  }
}
.navbar {
    overflow: hidden;
    background: rgba(255, 255, 255, 0.6);
    -webkit-backdrop-filter: saturate(100%) blur(7px);
            backdrop-filter: saturate(100%) blur(7px);
    position: relative;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
}

.navbar__memberBtn {
    position: absolute;
    top: 20%;
    right: 1%;
}

.nav-comp {
    position: absolute;
    z-index: 1;
    top: 1.3rem;
    left: 18vw;
    margin: auto;
    width: 65%;
    font-size: 1.6rem;
    color: black;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.nav-comp > span:hover {
    color: #EC3750;
    cursor: pointer;
    transition: 0.3s ease;
}

.navbar a {
    text-decoration: none;
    color: black;
}

.navbar a:hover {
    color: #EC3750;
    text-decoration: underline;
    -webkit-text-decoration-style: wavy;
            text-decoration-style: wavy;
    transition: 0.3s ease;
}

@media only screen and (max-width: 786px)
{
    .navbar__memberBtn {
        /* right: 20%; */
    }
}

@media only screen and (max-width: 400px)
{
    .navbar__memberBtn {
        /* right: 25%; */
    }
}
.philosophy {
    height: 1005;
    overflow: hidden;
    text-align: center;
    background: url("/static/media/BG(1).caab08d3.png");
}

.philosophy h1 {
    font-size: 4rem;
    padding-top: 3rem;
}

.philosophy h2 {
    font-size: 4rem;
    margin-top: 4rem;
}

.philosophy p {
    font-size: 2rem;
    margin: 8rem 9rem 0;
}

.philosophy Button {
    background: linear-gradient(270.92deg, #6F31B7 0%, #FB558E 100%, #FB558E 100%);
    font-size: 2rem;
    margin: 4rem 0;
}

@media only screen and (max-width: 768px) {
    .philosophy h1 {
        font-size: 3rem;
    }

    .philosophy h2 {
        font-size: 2.5rem;
    }

    .philosophy p {
        font-size: 1.75rem;
        margin: 2rem 1rem;
    }

    .philosophy Button {
        margin: 2rem 0;
    }
}
.perks {
    background: #fff;
    color: black;
    position: relative;
}

.perks h1 {
    font-size: 4rem;
    text-align: center;
    padding-top: 2rem;
    color: #EC3750;
}

.grid-container {
    display: grid;
    grid-gap: 4rem;
    gap: 4rem;
    margin: 2rem auto 0;
    max-width: 80%;
}

.one, .two, .three, .four {
    margin: 2% 7%;
}

.bounty {
    position: absolute;
    top: 12vh;
    right: 0vw;
    max-width: 13vw;
}

.zoom {
    position: absolute;
    top: 21vh;
    left: 3vw;
    width: 7vw;
}

.basket {
    position: absolute;
    top: 400px;
    left: 3vw;
    width: 7vw;
}

.musk-text {
    width: 25%;
    position: absolute;
    bottom: 5%;
    left: 35%;
}

.musk-text p {
    font-size: 20px ;
}

.stickers-text {
    color: #EC3750;
    font-size: 2rem;
    position: absolute;
    right: 5vw;
    bottom: 420px;
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
}

.stickers {
 position: relative;
}

.mac, .coke, .stranger, .jur {
    position: absolute;
}

.mac {
    bottom: 180px;
    right: 300px;
}

.coke {
    bottom: 200px;
    right: 30px;
}

.stranger {
    bottom: 0;
    right: 270px;
}

.jur {
    bottom: 0;
    right: 0;
}

.card-semi h2 {
    font-size: 1.75rem;
}

.card-semi p {
    font-size: 1.25rem;
}

@media (min-width: 600px) {
    .grid-container
    {
        
        grid-template-columns: repeat(2, 1fr); 
    }

    .one {
        grid-column: 1/2;
        grid-row: 1/2;
    }
    
    .two {
        grid-column: 1/2;
        grid-row: 2/2;
    }
    
    .three {
        grid-column: 2/2;
        grid-row: 1/2;
        text-align: right;
    }
    
    .four {
        grid-column: 2/2;
        grid-row: 2/2;
        text-align: right;
    }
  }

@media only screen and (max-width: 786px)
{
    .grid-container {
        grid-gap: 0;
        gap: 0;
        margin: 0;
        max-width: 100%;
    }

    .bounty, .zoom, .basket {
        display: none;
    }

    .musk, .musk-text {
        display: none;
    }

    .stickers-text {
        bottom: 90%;
        font-size: 24px;
    }

    .mac, .coke, .stranger, .jur {
        position: unset;
        max-width: 50%;
    }
}
