@import url("https://assets.hackclub.com/fonts/Phantom_Sans_0.7/Bold.woff");

.landing {
  height: 100vh;
  font-family: "Phantom Sans";
  color: #ffffff;
  overflow: hidden;
  box-sizing: content-box;
  background: linear-gradient(180deg, transparent, white) ,url('/src/images/BG.png');
  background-color: white;
}

.landing__content {
  font-size: 4rem;
  font-weight: 900;
  margin-top: 25vh;
  display: grid;
  align-items:center;
  color: #EC3750;
}

.s1{
  margin-left: 30vw;
}
.s2{
  margin-left: 35vw;
}
.s3{
  margin-left: 40vw;
}

.paral {
  display: inline-block;
  transform: skew(20deg);
  background: linear-gradient(90deg, #EC37A5 -22.7%, #EC3761 79.23%);
  width: 280px;
  margin-left: 50vw;
}

.paral .paraltext {
  display: block;
  transform: skew(-20deg);
  color: white;
  margin-left: 8%;
}

.paral1 {
  display: inline-block;
  transform: skew(20deg);
  background: #FF8C37;
  width: 150px;
}
.paral1 .paraltext1 {
  display: block;
  transform: skew(-20deg);
  color: white;
  margin-left: 5%;
}

@media only screen and (max-width: 792px) {
  .landing__content {
    display: flex;
    flex-direction: column;
  }
  .s1, .s2, .s3, .paral {
    margin-left: unset;
  }

  h2 {
    font-size: 1.2em;
  }

  .paral1 {
    width: 108%;
  }
}