.perks {
    background: #fff;
    color: black;
    position: relative;
}

.perks h1 {
    font-size: 4rem;
    text-align: center;
    padding-top: 2rem;
    color: #EC3750;
}

.grid-container {
    display: grid;
    gap: 4rem;
    margin: 2rem auto 0;
    max-width: 80%;
}

.one, .two, .three, .four {
    margin: 2% 7%;
}

.bounty {
    position: absolute;
    top: 12vh;
    right: 0vw;
    max-width: 13vw;
}

.zoom {
    position: absolute;
    top: 21vh;
    left: 3vw;
    width: 7vw;
}

.basket {
    position: absolute;
    top: 400px;
    left: 3vw;
    width: 7vw;
}

.musk-text {
    width: 25%;
    position: absolute;
    bottom: 5%;
    left: 35%;
}

.musk-text p {
    font-size: 20px ;
}

.stickers-text {
    color: #EC3750;
    font-size: 2rem;
    position: absolute;
    right: 5vw;
    bottom: 420px;
    transform: rotate(-15deg);
}

.stickers {
 position: relative;
}

.mac, .coke, .stranger, .jur {
    position: absolute;
}

.mac {
    bottom: 180px;
    right: 300px;
}

.coke {
    bottom: 200px;
    right: 30px;
}

.stranger {
    bottom: 0;
    right: 270px;
}

.jur {
    bottom: 0;
    right: 0;
}

.card-semi h2 {
    font-size: 1.75rem;
}

.card-semi p {
    font-size: 1.25rem;
}

@media (min-width: 600px) {
    .grid-container
    {
        
        grid-template-columns: repeat(2, 1fr); 
    }

    .one {
        grid-column: 1/2;
        grid-row: 1/2;
    }
    
    .two {
        grid-column: 1/2;
        grid-row: 2/2;
    }
    
    .three {
        grid-column: 2/2;
        grid-row: 1/2;
        text-align: right;
    }
    
    .four {
        grid-column: 2/2;
        grid-row: 2/2;
        text-align: right;
    }
  }

@media only screen and (max-width: 786px)
{
    .grid-container {
        gap: 0;
        margin: 0;
        max-width: 100%;
    }

    .bounty, .zoom, .basket {
        display: none;
    }

    .musk, .musk-text {
        display: none;
    }

    .stickers-text {
        bottom: 90%;
        font-size: 24px;
    }

    .mac, .coke, .stranger, .jur {
        position: unset;
        max-width: 50%;
    }
}