.teamCard {
    display: flex;
    width: 100%;
}

.teamCard__left {
    flex: 0.2;
}

.teamCard__left img {
    height: 100px;
    border-radius: 99999px;
}

.teamCard__social {
    display: flex;
    justify-content: space-between;
    width: 6vw;
}

.teamCard__right {
    display: flex;
    flex-direction: column;
    flex: 0.8;
}

.teamCard__right span {
    overflow: hidden;
    text-overflow: ellipsis;
}
