.footer {
    background-color: #1f2d3d;
    position: relative;
    color: white;
}

.logo {
    max-width: 10vw;
}

.just {
    padding: 5% 2%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.grid {
    font-size: 1.5em;
    display: flex;
    max-width: 30%;
    margin: auto !important;
    padding: 2vh 0;
    justify-content: space-between;
    padding-bottom: 2%;
}

@media only screen and (max-width: 786px)
{
    .logo {
        display: none;
    }

    .grid {
        max-width: 80%;
    }
}