.philosophy {
    height: 1005;
    overflow: hidden;
    text-align: center;
    background: url('../../images/BG\(1\).png');
}

.philosophy h1 {
    font-size: 4rem;
    padding-top: 3rem;
}

.philosophy h2 {
    font-size: 4rem;
    margin-top: 4rem;
}

.philosophy p {
    font-size: 2rem;
    margin: 8rem 9rem 0;
}

.philosophy Button {
    background: linear-gradient(270.92deg, #6F31B7 0%, #FB558E 100%, #FB558E 100%);
    font-size: 2rem;
    margin: 4rem 0;
}

@media only screen and (max-width: 768px) {
    .philosophy h1 {
        font-size: 3rem;
    }

    .philosophy h2 {
        font-size: 2.5rem;
    }

    .philosophy p {
        font-size: 1.75rem;
        margin: 2rem 1rem;
    }

    .philosophy Button {
        margin: 2rem 0;
    }
}