.navbar {
    overflow: hidden;
    background: rgba(255, 255, 255, 0.6);
    backdrop-filter: saturate(100%) blur(7px);
    position: relative;
    justify-content: space-between;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10;
}

.navbar__memberBtn {
    position: absolute;
    top: 20%;
    right: 1%;
}

.nav-comp {
    position: absolute;
    z-index: 1;
    top: 1.3rem;
    left: 18vw;
    margin: auto;
    width: 65%;
    font-size: 1.6rem;
    color: black;
    display: flex;
    justify-content: space-around;
}

.nav-comp > span:hover {
    color: #EC3750;
    cursor: pointer;
    transition: 0.3s ease;
}

.navbar a {
    text-decoration: none;
    color: black;
}

.navbar a:hover {
    color: #EC3750;
    text-decoration: underline;
    text-decoration-style: wavy;
    transition: 0.3s ease;
}

@media only screen and (max-width: 786px)
{
    .navbar__memberBtn {
        /* right: 20%; */
    }
}

@media only screen and (max-width: 400px)
{
    .navbar__memberBtn {
        /* right: 25%; */
    }
}