.next {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background: url('../../images/BG\(2\).png');
}

.next h1 {
    font-size: 4rem;
    padding-top: 3rem;
}

.next h2 {
    font-size: 4rem;
    margin-top: 4rem;
}

.next p {
    font-size: 1.75rem;
    /* margin: 8rem 9rem 0; */
}

.next Button {
    font-size: 2rem;
    margin: 0 0 4rem;
}

.carddd {
    margin: 5rem !important;
    
}

.box {
    text-align: left;
    background-image: radial-gradient(at left top, rgb(91, 192, 222), rgb(51, 142, 218));
}

@media only screen and (max-width: 768px) {
    .next h1 {
        font-size: 3rem;
    }

    .next h2 {
        font-size: 2.5rem;
        margin: 0 2%;
    }

    .next p {
        font-size: 2rem;
        margin: 1rem 1rem;
    }

    .carddd {
        margin: 1rem !important;
    }
}